import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import TestComp from './views/TestComp.vue'
import './plugins/element.js'
Vue.config.productionTip = true
Vue.use(VueRouter)
const routes = [
  {
    path: '/',
    redirect: '/home'
},
  {
    path: '/music',
    name: 'music',
    component: TestComp
  }
]
const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

new Vue({
  router,
  render: h => h(App),
}).$mount('#app')
